@import url("https://fonts.googleapis.com/css?family=Fjalla+One");
.header {
  background: #fff;
  text-align: center;
  width: 100%;
  padding-top: 20px;
  margin-bottom: 20px; }

.balance, .income, .outcome {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px; }
  @media (min-width: 800px) {
    .balance, .income, .outcome {
      padding-left: 200px;
      padding-right: 200px; } }
  @media (min-width: 500px) {
    .balance, .income, .outcome {
      padding-left: 100px;
      padding-right: 100px; } }

.balance h1, .income h2, .outcome h2 {
  padding-left: 2.5px;
  padding-right: 2.5px; }



